<template>
  <div>
    <div v-if="file">
      <h1 class="subtitle title--no-sp is-3">{{ file.name }}</h1>
      <span class="subtitle is-6">
        {{ file.downloadCount }}
        {{ $t("file.downloads") }} / {{ file.visitCount }} {{ $t("file.visits") }}
      </span>
      <div class="mt-6">
        <Adsense data-ad-client="ca-pub-4679085340013866" data-ad-slot="6511749031"></Adsense>
        <div class="mt-4">
          <a
            :href="`${file.url}`"
            class="button is-danger"
            :class="{ 'pointer-events-none': downloaded }"
            target="_blank"
            @click="onFileDownload"
            :disabled="downloaded"
          >{{ $t("file.downloadFile") }}</a>
        </div>
        <Adsense data-ad-client="ca-pub-4679085340013866" data-ad-slot="6511749031"></Adsense>
      </div>
    </div>
    <div class="mt-6">
      <article class="message max-w-md mr-auto ml-auto">
        <div class="message-header">
          <p class="mb-0">What are Cloud Service Providers?</p>
        </div>
        <div class="message-body">
          Cloud service providers (CSP) are companies that offer network services,
          infrastructure, or business applications in the cloud. The cloud services are
          hosted in a data center that can be accessed by companies or individuals
          using network connectivity.
          The large benefit of using a cloud service provider comes in efficiency
          and economies of scale. Rather than individuals and companies building
          their own infrastructure to support internal services and applications,
          the services can be purchased from the CSP, which provide the services to
          many customers from a shared infrastructure.
          There are several different forms of services that can be used “in the cloud”
          by CSPs, including software, often referred to as Software as a Service (SaaS),
          a computing platform for developing or hosting applications, known as Platform
          as a Service (PaaS); or an entire networking or computing infrastructure, known as
          Infrastructure as a Service (IaaS). The divisions, however, are not always clear-cut,
          as many providers may offer multiple flavors of cloud services, include traditional web
          or application hosting providers. For example, you might go to a cloud provider, such as
          Rackspace, who started as a web hosting company and buy either PAAS or IAAS services.
          Many cloud providers are focusing on specific verticals, such as hosting healthcare
          applications in a secure IAAS environment.
        </div>
      </article>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import EventBus from '@/eventBus';

export default {
  data: () => ({
    file: null,
    title: '',
    downloaded: false,
  }),
  mounted() {
    axios
      .get(`/api/files/${this.$route.params.id}`)
      .then(({ data: file }) => {
        this.file = file;
        this.file.visitCount += 1;
        this.title = `${file.name} - `;
      })
      .catch((err) => {
        console.log(err);

        if (err.response.status === 500) {
          return EventBus.$emit(
            'error',
            'Server Error: Unable to handle the request now. Please try again later.',
          );
        }

        return this.$router.push('/404');
      });
  },
  methods: {
    onFileDownload() {
      this.file.downloadCount += 1;
      this.downloaded = true;

      axios.post(`/api//download/${this.file.encodedName}`);
    },
  },
  metaInfo() {
    return {
      title: this.title,
    };
  },
};
</script>
